import React from 'react';
import { Divider } from 'antd';
import { Toolbar } from 'gg-editor';
import Button from './Button';
import styles from './index.css';

class UMindToolbar extends React.Component {
  render() {
    return (
      <Toolbar className={styles.toolbar}>
        <Button name="save" icon="iconSave" text="" hotkey="+S" />
        <Divider type="vertical" />
        <Button name="undo" icon="iconUndo" text="" hotkey="+Z" />
        <Button name="redo" icon="iconRedo" text="" hotkey="+Shift+Z" />
        <Divider type="vertical" />
        <Button name="append" icon="iconAppend" text="" hotkey="Enter" />
        <Button name="appendChild" icon="iconAppendChild" text="" hotkey="Tab" />
        <Divider type="vertical" />
        <Button name="collapse" icon="iconCollapse" text="" hotkey="+/" />
        <Button name="expand" icon="iconExpand" text="" hotkey="+/" />
      </Toolbar>
    );
  }
}

export default UMindToolbar;
